ul {
  list-style: none;
  margin: 0;
  padding: 0;
  cursor: pointer;
}

body {
  background: #f8f6f6;
  font-family: cursive;
  font-weight: normal;
}

.todo {
  position: relative;
  width: 100vh;
  margin-left: auto;
  margin-right: auto;
  padding: 4vh 0;
  background: #fff;
  border: 1px solid;
  border-color: #dfdcdc #d9d6d6 #ccc;
  border-radius: 2px;
  -webkit-box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
}

.todo:before,
.todo:after {
  content: '';
  position: absolute;
  z-index: -1;
  height: 4px;
  background: #fff;
  border: 1px solid #ccc;
  border-radius: 2px;
}

.todo:after {
  left: 0;
  right: 0;
  bottom: -3px;
  -webkit-box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
}

.todo:before {
  left: 2px;
  right: 2px;
  bottom: -5px;
  border-color: #c4c4c4;
  -webkit-box-shadow: 0 1px 2px rgba(0, 0, 0, 0.15);
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.15);
}

/*.lt-ie9 .todo:before,
.lt-ie9 .todo:after {
    height: 1px;
    border-top: 0;
}*/

.todo-list {
  border-top: 1px solid #e6ebed;
}

.form-wrapper {
  margin-right: auto;
  margin-left: auto;
  z-index: 200;
  position: relative;
  text-align: center;
  top: 9vh;
  width: 96.5vh;
  padding: 1.9vh;
  background: #cccccc;
  -webkit-box-shadow: 0 1px 1px rgba(0, 0, 0, .4) inset,
    0 1px 0 rgba(255, 255, 255, .2);
  box-shadow: 0 1px 1px rgba(0, 0, 0, .4) inset, 0 1px 0 rgba(255, 255, 255, .2);
}

.button:before {
  content: '';
  position: absolute;
  border-width: 1.2vh 1.2vh 1.2vh 0vh;
  border-style: solid solid solid none;
  border-color: transparent #d83c3c transparent;
  top: 1.4vh;
  left: -1vh;
}

.todo-list:after {
  content: '';
  width: 0.4vh;
  z-index: 2;
  border: 1px solid #f2e3df;
  border-width: 0 1px;
  position: absolute;
  top: 0vh;
  bottom: 0vh;
  left: 4.9vh;
}

.todo-list li {
  position: relative;
  padding: 2.2vh 15px 2vh 7.9vh;
  line-height: 5vh;
  font-size: 3vh;
  color: #8b8f97;
  border-bottom: 1px solid #e6ebed;
}
.editItem {
  margin: 0 !important;
  padding: 0 !important;
}

li:nth-child(odd) {
  background: #f9f9f9;
}

.linet {
  text-decoration: line-through;
  background-color: #e0e0e0 !important;
}

li:hover {
  background: #eaeaea;
}

.signc {
  border: 0;
  background: transparent;
  line-height: 5vh;
  font-size: 3vh;
  color: #8b8f97;
  cursor: pointer;
  font-family: cursive;
  font-weight: normal;
  position: absolute;
  right: 0;
  top: 0;
  padding: 2vh 2.5vh 2vh 2.5vh;
}

.signc:hover {
  background-color: #f44336;
  color: white;
}

.form-wrapper input:focus {
  outline: 0;
  background: #fff;
  -webkit-box-shadow: 0 0 2px rgba(0, 0, 0, .8) inset;
  box-shadow: 0 0 2px rgba(0, 0, 0, .8) inset;
}

.form-wrapper input::-webkit-input-placeholder {
  color: #999;
  font-weight: normal;
  font-style: italic;
}

input {
  font-family: inherit;
  font-size: inherit;
  width: 94%;
  outline: none;
  text-decoration: inherit;
}
.form-wrapper input:-moz-placeholder {
  color: #999;
  font-weight: normal;
  font-style: italic;
}

.form-wrapper input:-ms-input-placeholder {
  color: #999;
  font-weight: normal;
  font-style: italic;
}

.button:hover {
  background: #e54040;
}

.button:active,
.button:focus {
  background: #c42f2f;
}

.button:hover:before {
  border-right-color: #e54040;
}

.button:focus:before {
  border-right-color: #c42f2f;
}

.button::-moz-focus-inner {
  border: 0;
  padding: 0;
}

.form-wrapper input {
  width: 82.4vh;
  height: 5vh;
  padding: 10px 5px;
  font: bold 15px 'lucida sans', 'trebuchet MS', 'Tahoma';
  border: 0;
  background: #eee;
  border-radius: 3px 0 0 3px;
}

.todo {
  padding: 8.9vh 0;
}

.closebtn {
  float: right;
  font-size: 30px;
  font-weight: bold;
  cursor: pointer;
}

.button {
  overflow: visible;
  position: relative;
  float: right;
  border: 0;
  padding: 0;
  cursor: pointer;
  height: 5vh;
  width: 14vh;
  font: bold 2vh 'lucida sans', 'trebuchet MS', 'Tahoma';
  color: #fff;
  text-transform: uppercase;
  background: #d83c3c;
  border-radius: 0 3px 3px 0;
  text-shadow: 0 -1px 0 rgba(0, 0, 0, .3);
}

.main {
  top: 4vh;
  position: relative;
}

.notepad {
  margin-left: auto;
  margin-right: auto;
  height: 4.0vh;
  width: 100.3vh;
  border-radius: 5px;
  -webkit-box-shadow: 0 5px 20px rgba(80, 0, 0, .3);
          box-shadow: 0 5px 20px rgba(80, 0, 0, .3);
  z-index: 500;
  position: relative;
}

.notepad h1 {
  text-align: center;
  font-size: 4vh;
  height: 7vh;
  line-height: 7vh;
  background-color: #3f7eb6;
  background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0)), to(rgba(0, 0, 0, .15)));
  background-image: -webkit-linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, .15));
  background-image: -o-linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, .15));
  background-image: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, .15));
  border-radius: 5px 5px 0 0;
  -webkit-box-shadow: inset 0 1px rgba(255, 255, 255, .3);
          box-shadow: inset 0 1px rgba(255, 255, 255, .3);
  border: 1px solid rgba(0, 0, 0, .5);
  padding: .75em;
  color: #fff;
  text-shadow: 0 1px 1px rgba(0, 0, 0, .2);
  margin: 0;
  position: relative;
}

.notepad h1:after {
  content: '';
  position: absolute;
  width: 100%;
  bottom: 0.5vh;
  left: 0;
  border-bottom: 0.2vh dashed rgba(0, 0, 0, .6);
}

.signcc {
  -webkit-transition: -webkit-transform .8s ease-in-out;
  transition: -webkit-transform .8s ease-in-out;
  -o-transition: transform .8s ease-in-out;
  transition: transform .8s ease-in-out;
  transition: transform .8s ease-in-out, -webkit-transform .8s ease-in-out;
  -webkit-transform-origin: 55% 55%;
      -ms-transform-origin: 55% 55%;
          transform-origin: 55% 55%;
}

.signc:hover .signcc {
  transform: rotate(360deg);
  -ms-transform: rotate(360deg);
  -webkit-transform: rotate(360deg);
}

form {
  margin-bottom: inherit;
  display: inherit;
  margin-top: inherit;
}

.eitem {
  background: green;
  width: 100%;
  left: 0;
}

.editimgbox {
  z-index: 20;
  position: absolute;
  left: 0;
  top: 0;
  height: 66px;
  width: 39px;
  border: 0;
  background: transparent;
}

.editimg {
  height: 5vh;
  left: 0.19vh;
  position: absolute;
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
  top: 1.8vh;
}

input:disabled,
textarea:disabled {
  color: inherit;
}
input {
  -webkit-appearance: inherit;
  background-color: inherit;
  -webkit-rtl-ordering: inherit;
  -webkit-user-select: inherit;
     -moz-user-select: inherit;
      -ms-user-select: inherit;
          user-select: inherit;
  cursor: inherit;
  border-width: inherit;
  border-style: inherit;
  border-color: inherit;
  -webkit-border-image: inherit;
       -o-border-image: inherit;
          border-image: inherit;
}

.currentlyBeingEditedImg {
  -webkit-filter: grayscale(0%);
          filter: grayscale(0%);
}

.loading {
  text-align: center;
  margin-top: 25vh;
}



